import Config from 'static/js/config.js'
export default {
	data: {
		return: {
			updataNews: null,
			userInfo: {},
			requset_url: ''
		}
	},
	onLaunch: function() {
		this.requset_url = Config.requset_url
		console.log('App Launch')
	},
	onShow: function() {
		console.log('App Show')
		// 首次刷新新消息条数
		this.alwaysUpdataNews()
		// 定时刷新新消息条数
		// this.updataNews = setInterval(() => {
		// 	this.alwaysUpdataNews()
		// }, 3000);
		// setTimeout(() => {
		// 	clearTimeout(this.updataNews);
		// }, 6000)
	},
	onHide: function() {
		console.log('App Hide')
		// 停止新消息的刷新
		clearTimeout(this.updataNews);
	},
	methods: {
		alwaysUpdataNews() {
			const that = this
			that.userInfo = that.$store.state.userInfo;
			// 判断用户信息是否已经获取
			if (that.userInfo.openid) {
				uni.request({
					url: `${that.requset_url}chat/unreadNumber`,
					data: {
						openid: that.userInfo.openid
					},
					method: 'POST',
					success: (res) => {
						let index = 1
						// that.$store.commit('setNewNewsNum', res.data.number);
						// console.log(that.$store.state.newNewsNum)
						if (res.data.number > 0) {
							// 添加底部导航右上角文字
							that.$store.commit('setNewNewsNum', res.data.number);
							uni.setTabBarBadge({
								index: index,
								text: res.data.number+''
							})
						} else {
							uni.removeTabBarBadge({
								index: index
							})
						}
					}
				});
			}
		}
	}

}
